$color-1 : #fbfbfbdb;
$color-2 : #1e272e;
$color-3 : #ff793f;
$color-4 : #bebdbd;
$color-5 : rgb(114, 107, 107);
$color-6 : #1a204f;

// #F2F3F4 blanc
// #212F3D Noir/bleu
// rgb(98, 204, 209) cyan



*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Montserrat';
    html{
        height: -webkit-fill-available;
    }
    
    body{
        background: $color-2;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        height: -webkit-fill-available;
        margin:auto;
        
        min-width: 350px;
        min-height: 700px;
        .navigation{
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            height: 3800px;
         
        }
    }
  }


