.navBar{
    height: 100px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    background: $color-2;
    position: fixed;
    right: 0;
    z-index: 10000000;
    animation: nav 0.3s ease forwards; 
   
    @keyframes nav {
        0%{
            opacity: 0;
        }
        100%{
            opacity:1;
        }
    }
    .logo{
        height: 40px;
        width: 40px;
       border: 2px solid $color-3;
        left: 40px;
        text-align: center;
        transform: rotate(45deg);
        position: relative;
        cursor: pointer;
        span{
            transform:  translateY(10px) rotate(-45deg);
            position: absolute;
            right: 8px;
            top: 0;
            font-size: 13px;
            color: $color-3;
        }
    }
    .nav-item{
        ul{
            display: flex;
            justify-content: space-between;
            list-style-type: none;
            position: relative;
            color:  $color-1;
            transition: all 0.5 ease;
            li{
                margin: 10px;
                padding: 10px;
                font-size: 12px;
                span{
                    img{
                        height: 15px;   
                    }
                }
                &:hover{
                    color: $color-3;
                    cursor: pointer;
                    transition: all 0.5 ease;
                }

            }
            li:nth-child(5),li:nth-child(6){
                border: 1px solid $color-3;
                text-decoration: none;
                border-radius: 10px;
                text-align: center;
                width: 130px;
                color: $color-1;
                transition: all 0.5 ease;
                    a:link{
                        color: $color-1;
                      text-decoration: none;
                        border: none;
                        span{
                            text-decoration: none;
                            &:hover{
                                color: $color-1;
                            }
                        }
                        
                    }
                    a:visited{
                        color: $color-1;
                      text-decoration: none;
                        border: none;
                    }
                    
            
                &:hover{
                    transition: all 0.5 ease;
                    background: $color-3;
                    opacity: 0.7;
                }
            }
        }
    }

    .nav-toggle{
        display: none;
        
    }
    @media screen and (max-width : 735px) {
        .nav-item{
            ul{
                display: flex;
                top: 60px;
                flex-direction: column;
                position: absolute;
                top: 90px;
                color: $color-1;
                background: $color-2;
                right: 0;
                transform: translateX(100%);
                z-index: 10000;
            }
        }
        .nav-item.open{
            ul{
                transform: translateX(0);
            }
           
        }
        
        
        .nav-toggle{
            display: flex;
            width: 50px;
            height: 50px;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            .bar{
                position: relative;
                width: 32px;
                height: 2px;
                background: $color-1;
                
            }
            .bar::before, .bar::after{
                content: "";
                position: absolute;
                height: 2px;
                background: $color-1;
                transition : all 0.45s ease-in-out;
            }
            .bar::before{
                width: 25px;
                transform: translateY(-8px);
                right: 0;
            }
            .bar::after{
                width: 32px;
                transform: translateY(8px);
            }
        }
        .nav-toggle.open > .bar{
            transform: translateX(-40px);
            background: transparent;
            
        }
        .nav-toggle.open > .bar::before{
            width: 32px;
            transform: rotate(45deg) translate(25px, -26px);
        }
        .nav-toggle.open > .bar::after{
            transform: rotate(-45deg) translate(26px,26px);

        }
      
}
    
}