.containerBienvenue {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 0 auto;

  .containerName {
    position: absolute;
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 500px;
    width: 500px;
    font-size: 16px;

    @media screen and (max-width: 1100px) {
      left: 150px;
    }
    @media screen and (max-width: 700px) {
      font-size: 14px;
      left: 100px;
    }
    @media screen and (max-width: 580px) {
      font-size: 11px;
      height: 450px;
      width: 320px;
      left: 20px;
    }
    @media screen and (max-width: 380px) {
      font-size: 8px;
      height: 350px;
      width: 220px;
      left: 70px;
    }

    h1 {
      font-size: 1em;
      color: #bdc3c7;
    }
    h2 {
      font-size: 3.1em;
      color: #ecf0f1;
      position: relative;
      width: 349px;
      @media screen and (max-width: 700px) {
        width: 305px;
      }
      @media screen and (max-width: 580px) {
        width: 240px;
      }
      @media screen and (max-width: 380px) {
        width: 180px;
      }

      &::before {
        content: "";
        position: absolute;
        height: 3vh;
        width: 100%;
        height: 90%;
        left: 100%;
        background: $color-2;
        border-left: 2px solid #ecf0f1;
        animation: typing1 1s steps(13) forwards, breath 1.5s infinite;
        @keyframes typing1 {
          0% {
            left: 0;
            width: 100%;
          }
          99% {
            opacity: 1;
          }
          100% {
            transform: translateX(100%);
            opacity: 0;
            width: 0%;
          }
        }
        @keyframes breath {
          99% {
            opacity: 1;
          }

          100% {
            opacity: 0;
          }
        }
      }
    }
    h4 {
      font-size: 1.8em;
      font-weight: bold;
      color: $color-3;
      position: relative;
      width: 455px;
      @media screen and (max-width: 700px) {
        width: 400px;
      }
      @media screen and (max-width: 580px) {
        width: 315px;
      }
      @media screen and (max-width: 380px) {
        width: 230px;
      }
      &::before {
        content: "";
        position: absolute;
        height: 3.2vw;
        width: 100%;
        height: 100%;
        left: 100%;
        background: $color-2;
        border-left: 2px solid $color-3;
        animation: typing2 2s steps(17) forwards, breath 2s infinite;
        @media screen and (max-width: 580px) {
          width: 50%;
        }
        @keyframes typing2 {
          0% {
            left: 0;
            width: 100%;
          }
          99% {
            opacity: 1;
          }
          100% {
            transform: translateX(100%);
            width: 0%;
            opacity: 0;
          }
        }
        @keyframes breath {
          99% {
            opacity: 1;
          }

          100% {
            opacity: 0;
          }
        }
      }
    }
  }
}
