.footer{
    position: absolute;
    width: 500px;
    text-align: center;
    color: $color-4;
    text-align: center;
    top: 1500px;
    left: 50%;
    transform: translateX(-50%);
    h6{
        margin: 20px;
    }

    span{
        height: 40px;
        width: 40px;
        border: 2px solid $color-3;
        transform: rotate(45deg);
        color: $color-3;
        padding: 5px;
    }

    @media screen and (max-width : 1100px)
    {
        top: 1850px;
    }


    @media screen and (max-width : 730px)
    {
        top: 2800px;
        width: 300px;
    }
    @media screen and (max-width : 620px)
    {
        top: 2950px;
        width: 300px;
    }
}