.projects2{
    height: 600px;
    width: 900px;
    font-size: 14px;
    position: absolute;
    top: 900px;
    @media screen and (max-width : 1100px )
    {
        height: 500px;
        width: 700px;
        font-size: 16px;
        top: 800px;
    }
    @media screen and (max-width : 833px)
    {
        height: 400px;
        width: 500px;
        font-size: 13px;
    }
    @media screen and (max-width : 620px)
    {
        height: 230px;
        width: 320px;
        top: 1580px;
        font-size: 12px;
        position: absolute;
    }

    .box2{
        margin-top: 50px;
        display: flex;
        justify-content: center;
        position: relative;
        .boxImage2{
            height: 350px;
            width: 400px;
            img{
                height: 350px;
                width: 500px;
                border-radius: 10px;
                transform: translate(20px, -20px);
                filter: opacity(0.1) drop-shadow(0 0 0 $color-3); 
                transition: all 0.25s;
                position: absolute;
                opacity: 0;
                @media screen and (max-width : 620px)
            {
                transform: translate(0px, 0px);
            }
                &:hover{
                    transition: all 0.25s;
                    filter: none; 
                    cursor: pointer;
                }
                @media screen and (max-width : 620px)
                {
                    height: 260px;
                    width: 350px;
                    top: 120px;
                    right: -16px;
                }
                @media screen and (max-width :380px)
                {
                    width: 300px;
                    top: 120px;
                    position: absolute;
                }

            }
            img:nth-child(1){
                opacity: 1;
                &:hover{
                    filter: opacity(0.2) drop-shadow(0 0 0 $color-3);
                }
                @media screen and (max-width : 620px)
                {
                    opacity: 0;
                }
            }
            img:nth-child(2){
                opacity: 0;
                z-index: 100;
                @media screen and (max-width : 620px)
                {
                    opacity: 1;
                    top: 97px;
                    left: 0px;
                    filter: opacity(0.8) drop-shadow(0 0 0 $color-3);
                    
                }
                @media screen and (max-width :380px)
                {
                    left: 30px;
                }
                &:hover{
                    opacity: 1;
                    
                    @media screen and (max-width : 620px)
                {
                    opacity: 1;
                    transform: none;
                }
                }
            }
        }
        .boxDescript2{
            
            height: 300px;
            width: 800px;
            position: relative;
            text-align: center;
           
            @media screen and (max-width : 833px)
            {
                position: relative;
                
            }
            @media screen and (max-width : 620px)
            {
                position: absolute;
                width: 350px;
                left: 0px;
            }
            @media screen and (max-width :380px)
            {
                width: 305px;
                left: 30px;
            }
            h3{
                color: $color-3;
                margin-top: 10px;
            }
            h6{
                font-style: italic;
                color: $color-4;
            }
            .boxDetail2{
                position: absolute;
                background: $color-6;
                height: 170px;
                width: 600px;
                right: 0px;
                top: 160px;
                border-radius: 10px;
                text-align: right;
                display: flex;
                border:4px solid $color-3;
                @media screen and (max-width : 833px)
                {
                width: 500px;
                }
                @media screen and (max-width : 620px)
                {
                    left: 0px;
                    width: 349px;
                    transform: translateY(118%);
                    text-align: center;
                    font-size: 11px;
                  
                }
                 @media screen and (max-width : 380px)
                {
                
                    width: 300px;
             
                  
                }
               
                h4{
                    margin: 10px;
                    span{
                        color: $color-3;
                        cursor: pointer;
                    }
                }
                img{
                    position: absolute;
                    height: 23px;
                    right: 0;
                    bottom: 0;
                    transform: translate(-5px, -5px);
                    cursor: pointer;
                    &:hover{
                        display: none;
                        cursor: pointer;
                    }
                }
                img:nth-child(2){
                    cursor: pointer;
                }
            }
        }
    }
}