.containerLoader{
display: flex;
justify-content: center;
align-items: center;
height: 100vh;
position: relative;
    .loader{
        transform: rotate(45deg);
        height: 80px;
        width: 80px;
        animation: load 3.5s ease-in-out forwards;
        animation-delay: 1s;
        @keyframes load {
            0%{
                border-top: 3px solid $color-3;
            }
            25%{
                border-right: 3px solid $color-3;
            }
            50%{
                border-bottom: 3px solid $color-3;
            }
            75%{
                border-left: 3px solid $color-3;
            }
        }
    }
    span{
        position: absolute;
        color : $color-3;
        font-weight: bold;
        font-size: 20px;
        transform: scale(0);
        animation: text1 4s linear forwards;
        animation-delay: 0.5s;
        @keyframes text1 {
            0%{
                transform: scale(1.8);
            }
            50%{
                transform: scale(1.8);
            }
            75%{
                transform: scale(1.5); 
            }
            100%{
                transform: scale(0);
            }
        }
    }
}

