.ContainerSideBar{
    position: relative;
    z-index: 10000;
    opacity: 0;
   
    
    .sideBar{
        position: fixed;
        height: 2px;
        width: 118px;
        background: $color-1;
        bottom: 50px;
        transform: rotate(90deg);
        @media screen and (max-width : 420px)
        {
            transform: translateX(-18px) rotate(90deg);
        }
        @media screen and (max-width : 370px)
        {
            transform: translateX(-23px) rotate(90deg);
        }
    }
    .mail{
        position: fixed;
        width: 220px;
        color: $color-1;
        left: -52px;
        bottom: 340px;
        transform: rotate(-90deg);
        font-size: 15px;
        cursor: pointer;
        &:hover{
            color : $color-3;
        }
        @media screen and (max-width : 420px)
        {
            font-size: 12px;
            transform: translateX(-20px) rotate(-90deg);
            
        }
        @media screen and (max-width : 370px)
        {
            font-size: 11px;
            transform: translateX(-6px) rotate(-90deg) ;
            width: 180px;
        }
    }
    animation: side 1s ease forwards; 
    animation-delay: 2s;
    @keyframes side {
        0%{
            opacity: 0;
        }
        100%{
            opacity:1;
        }
    }
 
    img{
        position: fixed;
        bottom : 169px ;
        left:48px;
        @media screen and (max-width : 420px)
        {
            transform: translateX(-20px);
        }
        @media screen and (max-width : 370px)
        {
            transform: translateX(-24px);
        }
      
        
    }
    
    img:nth-child(1){
        z-index: 100;
        height: 25px;
        width:25px;
        cursor: pointer;
        &:hover{
            opacity: 0;
        }
        @media screen and (max-width : 370px)
        {
            height: 20px;
            width:20px;
            left: 29px;
            bottom: 160px;
        }
      
    }
    img:nth-child(2){
        height: 25px;
        width: 25px;
        @media screen and (max-width : 370px)
        {
            height: 20px;
            width:20px;
            left: 29px;
            bottom: 160px;
        }
        
    }
    img:nth-child(3){
        z-index: 100;
        left: 49px;
        height: 25px;
        width:25px;
        bottom: 129px;
        cursor: pointer;
        &:hover{
            opacity: 0;
        }
        @media screen and (max-width : 370px)
        {
            height: 20px;
            width:20px;
            left: 29px;
        }
    }
    img:nth-child(4){
        
        bottom: 129px;
        height: 25px;
        width: 25px;
        @media screen and (max-width : 370px)
        {
            height: 20px;
            width:20px;
            left: 29px;
        }
        
    }

}

@media screen and (max-width : 1100px)
{
    .ContainerSideBar{
        .sideBar{
        left: -20px;
           
        }
        .mail{
            left: -70px;
        }
        img{
            left: 30px;
        }
        img:nth-child(3), img:nth-child(4){
            left: 30px;
        }
    }
}



    
