.containerAbout{
   height: 100vh;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
   position: relative;
   font-size: 15npx;
   
        @media screen and (max-width : 1100px )
        {
            font-size: 16px;
        }
        @media screen and (max-width : 833px)
        {
            font-size: 13px;
        }
        @media screen and (max-width : 620px)
        {
            font-size: 12px;
        }
   .lgAbout{
       color: $color-1;
       height: 600px;
       width: 900px;
       position: absolute;
       
       @media screen and (max-width : 1100px )
        {
            height: 500px;
            width: 700px;
        }
        @media screen and (max-width : 833px)
        {
            height: 400px;
            width: 500px;
        }
        @media screen and (max-width : 620px)
        {
            height: 250px;
            width: 350px;
            top: 150px;
        }
        @media screen and (max-width :380px)
        {
            height: 200px;
            width: 300px;
        }

        .fr{
            @media screen and (max-width : 620px)
        {
            height: 0px;
            width: 350px;
            top: 150px;
        } 
        h2{
            @media screen and (max-width : 620px)
        {
            height: 0px;
            width: 270px;
            top: 270px;
            left: 20px;
        } 
        }
        }
        
       button{
        width: 80px;
        height: 40px;
        border-radius: 20px;
        margin: 10px;
        cursor: pointer;
        background: $color-3;
            
            &:hover{
            transform: scale(1.1);
            }    
        }
       h1{
        font-size: 2em;
        letter-spacing: 1px;
        margin: 10px;
        position: relative;
       
        &::after{
            position: absolute;
            content: "";
            width: 200px;
            opacity: 0.3;
            height: 2px;
            background: $color-4;
            top: 50%;
            left: 285px;
            @media screen and (max-width : 1100px )
            {
                width: 130px;
                left: 230px;
            }
            @media screen and (max-width : 833px)
            {
                width: 60px;
                left: 190px;
            }
            @media screen and (max-width : 620px)
        {
            width: 90px;
            left: 170px;
        }
        }
        
       }
      
       h2{
           position: absolute;
           font-size: 0.9em;
           width: 450px;
           margin: 10px;
           letter-spacing: 1px;
           font-weight: normal;
           @media screen and (max-width : 1100px )
            {
                width: 340px;
            }
            @media screen and (max-width : 833px)
            {
                width: 250px;
            }
            @media screen and (max-width : 620px)
            {
               top: 210px;
               text-align: center;
                left: 22%;
               
                
            }
           
           img{
                height: 20px;
                width: 20px;
           }
           ul{
               font-size: 0.8em;
               list-style: none;
               
           }
       }
       span{
           color: $color-3;
       }
       .en{
           position: absolute;
           top: 50px;
       }
       .portrait{
           position: relative;
           transition: revert 0.5s ease;
           border-radius: 20px;
           height: 600px;
            width: 900px;
 
            @media screen and (max-width : 1100px )
            {
                height: 500px;
                width: 700px;
            }
            @media screen and (max-width : 833px)
            {
                height: 400px;
                width: 500px;
            }
           @media screen and (max-width : 620px)
           {
            height: 220px;
            width: 350px;
           }
           
           img{
            height: 300px;
            position: absolute;
            left: 550px;
            border-radius: 20px;
            filter: opacity(0.6) drop-shadow(0 0 0 $color-3); 
            transition: all 0.25s;
            @media screen and (max-width : 630px)
            {
                right: 150px;
            }
            &:hover{
                transition: all 0.5s;
                transform: translateX(-20px) translateY(20px);
                filter: none;
                opacity: 1;
                @media screen and (max-width : 833px)
                {
                    transform: none;
                }
            }
            @media screen and (max-width : 1100px )
            {
                height: 250px;
                left: 400px;
            }
            @media screen and (max-width : 833px)
            {
                width: 190px;
                height: 210px;
                left: 290px;
                position: absolute;
            }
            @media screen and (max-width : 620px)
            {
                width: 130px;
                height: 140px;
                left: 28%;
                top: 50px;
                margin-bottom: 50px;
               

            }

           }
           
           
        } 

    }

}
  
