.boxEnter{
    margin-top: 30px;
    display:flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    transform: translateX(75%);
    @media screen and (max-width : 580px)
        {
            transform: translateX(60%)
        }
    @media screen and (max-width : 380px)
    {
        transform: translateX(0%)
    }
    .enter{
        position: absolute;
        width: 170px;
        height : 170px;
        border-radius: 50%;
        animation: circle 2s linear infinite;
        @media screen and (max-width : 580px)
        {
            width: 150px;
            height : 150px;
        }
        @media screen and (max-width : 380px)
        {
            width: 120px;
            height : 120px;
        }
        @keyframes circle {
            0%{
                transform: rotate(0deg);
                box-shadow: 4px 2px 10px $color-3;
            }
            50%{
                transform: rotate(180deg);
                box-shadow: 4px 2px 10px $color-3;
            }
            100%{
                transform: rotate(360deg);  
                box-shadow: 4px 2px 10px $color-3;
            }


        }  
        &::before{
            position: absolute;
            content: "";
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
            border-radius: 50%;
            box-shadow: 0 0 10px rgba(255, 255, 255, 0.141);
        }
    }

    .nav-active{
        border: none;
        background: none;
        text-decoration: none;
        color :$color-1;
        font-size: 20px;
        text-transform: uppercase;
        letter-spacing: 2px;
        line-height:200px ;
        cursor: pointer;
        z-index: 1000;
        animation: text 2s ease forwards;
        @keyframes text {
           0%{
                transform: scale(0);
            }
        }
        &:hover{
            transition: 2s ease;
            transform: scale(1.1);
        }
        @media screen and (max-width : 380px)
        {
            font-size: 13px;
        }

    }
}