.firstPageContainer{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    position: relative;
    .page1{
        height: 600px;
        width: 900px;
        opacity: 0;
        left: 40%;
        transform: translateY(200px);
        animation: jlAnim 1s ease forwards;
        animation-delay: 0.8s;
       
        @media screen and (max-width : 1100px )
        {
            height: 500px;
            width: 700px;
        }
        @media screen and (max-width : 900px)
        {
            height: 400px;
            width: 600px;
        }
        @media screen and (max-width : 760px)
        {
            height: 250px;
            width: 400px;
        }
        @media screen and (max-width : 580px)
        {
            height: 220px;
            width: 340px;
        }
        @media screen and (max-width :464px)
        {
            height: 200px;
            width: 320px;
        }
        @media screen and (max-width :380px)
        {
            height: 200px;
            width: 270px;
        }
        @keyframes jlAnim {
            0%{
                opacity: 0;
            }
            50%{
                left: -100%;
                opacity: 0;
            }
            100%{
                left: 40%;
                opacity: 1;
            }
        }
        .bio{
            font-size: 16px;
            @media screen and (max-width : 1100px )
            {
                font-size: 15px;
            }    
            @media screen and (max-width : 900px )
            {
                font-size: 13px;
            }   
            @media screen and (max-width : 760px )
            {
                font-size: 10px;
            }  
            
                img{
                    height: 25px;
                    width: 25px;
                    &:hover{
                        transform: rotate(40deg);
                    }
                }
                h1{
                    color: $color-1;
                    font-size: 1.3em;
                }
                h2{
                    font-size: 4em;
                    color: $color-3;
                    text-shadow: 2px 2px 5px;
                    width: 500px;
                    @media screen and (max-width : 580px)
                    {
                        width : 400px
                    }
                    @media screen and (max-width :464px)
                    {
                        width: 300px;
                    }
                }
                h3{
                    color: $color-4;
                    left: 270px;
                    width: 650px;
                    font-size: 1.5em;
                    @media screen and (max-width : 760px)
                    {
                        width: 400px;
                    }
                    @media screen and (max-width :464px)
                    {
                        width: 300px;
                    }
                }
                h4{
                    position: absolute;
                    top: 200px;
                    font-size: 1.6em;
                    color: $color-1;
                    @media screen and (max-width : 760px )
                    {
                        top: 130px;
                        width: 420px;
                    }
                    @media screen and (max-width : 560px )
                    {
                        top: 130px;
                        width: 380px;
                    }
                    @media screen and (max-width :464px)
                    {
                        width: 300px;
                    }
                }
                h5{
                    position: absolute;
                    top: 320px; 
                    color: $color-1;
                    font-size: 1.3em;
                    color: $color-1;
                    border: 2px solid $color-1;
                    padding: 10px;
                    cursor: pointer;
                    border-radius: 10px;
                    transition: all 0.2s ease;
                    &:hover{
                        transition: all 0.2s ease;
                        color: $color-3;
                        transform: scale(1.1);
                        border: 2px solid $color-3;
                    }
                    @media screen and (max-width : 760px )
                    {
                        top: 220px; 
                    }
                }
                span{
                    color: $color-3;
                }
            }
        
    }
    
}
